body {
  background-color: #ffffff;
  color: #000000;

  font-family: 'Roboto', sans-serif;
  font-style: normal;
  font-size: 12px;
  font-weight: 500;
  line-height: 1.67;
}

h1,
h2,
h3,
h4,
h5 {
  margin: 0px;
}

a {
  text-decoration: none;
}
img {
  display: block;
  max-width: 100%;
  height: auto;
}

ul {
  list-style: none;
  margin: 0;
  padding: 0;
}

p {
  margin: 0;
}
